export default {
	data() {
		return {
			swiperList: [],
			dotStyle: false,
			cardCur: 0,
			placeNo: '',
			list: [],
			CheckTime: '',
			CheckUser: '',
			PlaceName: '',
			time: ''
		}
	},
	methods: {
		ViewImage(e) {
			uni.previewImage({
				urls: e.currentTarget.dataset.all,
				current: e.currentTarget.dataset.url
			});
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		getDetail() {
			var self = this;
			uni.request({
				//当前详情
				url: self.$store.state.urlPrefix + '/school/Get_HDDetail',
				data:{
					placeNo: self.placeNo,
					type: 'KJ'
				}
			}).then(data => {
				var [error,res] = data;
				self.list = res.data.data[0];
				self.CheckTime = res.data.data[0].CheckTime;
				self.CheckUser = res.data.data[0].CheckUser;
				self.PlaceName = res.data.data[0].PlaceName;
				console.log(self.list.Detail)
				for (let site of self.list.Detail) {
					site.ImgURL = site.ImgURL.split(',')
				}
				uni.hideLoading()
			})
		},
		getHis() {
			// /Web/Get_query_hdDetail
			var time = this.time;
			
			time = time.replace(/\//g, '-');
			var self = this;
			var data = {};
			data = {
			  campusplaceNo: 'S000099100001',
			  placeNo: self.placeNo,
			  type: "KJ",
			  time: time,
			  YHXH: '',
			  CCType: ''
			}
			uni.request({
				//当前详情
				url: self.$store.state.urlPrefix + '/Web/Get_query_hdDetail',
				data: data
			}).then(data => {
				
				var [error,res] = data;
				self.list = res.data.data;
				self.CheckTime = res.data.data.CheckTime;
				self.CheckUser = res.data.data.CheckUserName;
				self.PlaceName = res.data.data.PlaceName;
				console.log(self.list.Detail)
				for (let site of self.list.Detail) {
					site.ImgURL = site.ImgURL.split(',')
				}
				uni.hideLoading()
			})
		}
	},
	mounted() {
		
		
	},
	onLoad(option) {
		uni.showLoading({
			title:"加载中。。。",
			mask:true
		})
		this.placeNo = option.placeNo;
		this.time = option.time;
		if (this.time) {
			
			this.getHis()
			
		} else {
			
			this.getDetail()
			
		}
	}
}
